        <h2>리뷰목록 -사업자-</h2>
    </div>
    <hr class="my-4" />
    <!-- 작성별 드롭다운 -->
    <div id="answerSort" class="dropdown">
        <button class="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
            작성별 정렬
        </button>
        <ul class="dropdown-menu">
            <li><a id="writeComplete" class="dropdown-item" href="#">작성 미완료</a></li>
            <li><a id="writeIncomplete" class="dropdown-item" href="#">작성 완료</a></li>
        </ul>
    </div>
    <!-- 시간별 드롭다운 -->
    <div id="timeSort" class="dropdown">
        <button class="btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
            시간별 정렬
        </button>
        <ul class="dropdown-menu">
            <li><a id="newest" class="dropdown-item" href="#">최신순</a></li>
            <li><a id="oldest" class="dropdown-item" href="#">오래된순</a></li>
        </ul>
    </div>
    <!-- 리뷰 리스트 -->
    <div class="reviewList">
        <div v-for="(review,i) in reviewList" :key="i">
            <div class="d-grid gap-2 col-10 mx-auto">
                <button class="btn btn-outline-dark" type="button" @click="moveComment">
                    <p class="text-start ">{{reviewDate[i]}}</p>
                    <span class="d-inline-block text-truncate" style="max-width: 200px;">
                        {{ reviewList [i]}}
                    </span>
                    <p class="text-end ">{{reviewId[i]}}</p>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                reviewDate: [
                    '2021-01-01',
                    '2021-01-02',
                    '2021-01-03',
                    '2021-01-04',
                    '2021-01-05'
                ],
                reviewList: [
                'first long review review review',
                'second long review review review',
                'third long review review review',
                'fourth long review review review',
                'fifth long review review review'
                ],
                reviewId: [
                'id-1',
                'id-2',
                'id-3',
                'id-4',
                'id-5'
                ]
            }
        },
        
        methods: {
            moveComment() {
            this.$router.push("/JY_B_ReviewComment")
            }
        }
    }
</script>

<style>
    .btn-outline-dark {
        margin-top: 10px;
        height: 100px;
    }
</style>